import { getNetEaseProduceMv } from '@/request/api/video.js';
export default {
  data() {
    return {
      // 网易出品 MV
      netEaseProduceMv: []
    };
  },

  mounted() {
    this.getNetEaseProduceMv();
  },

  methods: {
    // 获取网易出品 mv
    async getNetEaseProduceMv() {
      const res = await getNetEaseProduceMv(); // console.log('获取网易出品 mv', res);

      this.netEaseProduceMv = res.data.data; // console.log('网易出品 mv', this.netEaseProduceMv);
    },

    // 对播放量的处理
    formatCount(num) {
      return this.$formatCount(num);
    },

    // 去往全部 MV页
    goMvRankingListPage() {
      this.$router.push({
        path: '/AllMv',
        query: {
          type: '网易出品',
          order: '最新'
        }
      });
    },

    // 去往 mv播放页
    goVideoPlayer(mvid) {
      this.$router.push({
        path: '/VideoPlayer',
        query: {
          mvid: mvid,
          type: 1
        }
      });
    },

    // 去往歌手页
    goSingerPage(id) {
      this.$router.push({
        path: '/SingerPage',
        query: {
          id: id
        }
      });
    }

  }
};