import { getHotMv } from '@/request/api/video.js';
export default {
  data() {
    return {
      // 热播 MV
      hotMv: []
    };
  },

  mounted() {
    this.getHotMv();
  },

  methods: {
    // 获取热播 mv
    async getHotMv() {
      const res = await getHotMv(); // console.log('获取热播 mv', res);

      this.hotMv = res.data.data; // console.log('热播 mv', this.hotMv);
    },

    // 对播放量的处理
    formatCount(num) {
      return this.$formatCount(num);
    },

    // 去往全部 MV页
    goMvRankingListPage() {
      this.$router.push({
        path: '/AllMv',
        query: {
          order: '最热'
        }
      });
    },

    // 去往 mv播放页
    goVideoPlayer(mvid) {
      this.$router.push({
        path: '/VideoPlayer',
        query: {
          mvid: mvid,
          type: 1
        }
      });
    },

    // 去往歌手页
    goSingerPage(id) {
      this.$router.push({
        path: '/SingerPage',
        query: {
          id: id
        }
      });
    }

  }
};