import { getLatestMv } from '@/request/api/video.js';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 可查地区
      areas: ['内地', '港台', '欧美', '日本', '韩国'],
      // 最新 MV
      latestMv: [],
      // 查找地区
      area: '无'
    };
  },

  mounted() {
    this.getLatestMv(8, '');
  },

  methods: {
    // 获取最新 mv
    async getLatestMv(limit, area) {
      if (this.area !== area) {
        // 显示加载
        this.isLoading = 1;
        this.area = area; // 第二个参数不填则为全部

        const res = await getLatestMv(limit, this.area); // console.log('获取最新 mv', res);

        this.latestMv = res.data.data; // console.log('最新 mv', this.latestMv);
        // 加载完成

        this.isLoading = 0;
      }
    },

    // 对播放量的处理
    formatCount(num) {
      return this.$formatCount(num);
    },

    // 鼠标放上 area 文字变黑
    areaMouseover(index) {
      if (this.$refs.area[index].style.color != 'rgb(231, 25, 25)') this.$refs.area[index].style.color = '#000';
    },

    // 鼠标移开 area 字色恢复
    areaMouseleave(index) {
      if (this.$refs.area[index].style.color != 'rgb(231, 25, 25)') this.$refs.area[index].style.color = '#929292';
    },

    // 去往全部 MV页
    goMvRankingListPage() {
      this.$router.push({
        path: '/AllMv',
        query: {
          area: this.area,
          order: '最新'
        }
      });
    },

    // 去往 mv播放页
    goVideoPlayer(mvid) {
      this.$router.push({
        path: '/VideoPlayer',
        query: {
          mvid: mvid,
          type: 1
        }
      });
    },

    // 去往歌手页
    goSingerPage(id) {
      this.$router.push({
        path: '/SingerPage',
        query: {
          id: id
        }
      });
    }

  }
};